import { Component, OnInit, ChangeDetectorRef, Input, Inject, trigger, state, animate, transition, style } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DialogService } from 'ng2-bootstrap-modal';
import { DOCUMENT } from "@angular/platform-browser";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';
import { SkillModalComponent } from '../../modals/skill-modal/skill-modal.component';
import { StatusMobileModalComponent } from '../../modals/mobile-modals/status-mobile-modal/status-mobile-modal.component';
import { CategoryMobileModalComponent } from '../../modals/mobile-modals/category-mobile-modal/category-mobile-modal.component';
import { NeedMobileModalComponent } from '../../modals/mobile-modals/need-mobile-modal/need-mobile-modal.component';
import { SkillMobileModalComponent } from '../../modals/mobile-modals/skill-mobile-modal/skill-mobile-modal.component';
import { LocationMobileModalComponent } from '../../modals/mobile-modals/location-mobile-modal/location-mobile-modal.component';

/* Services */
import { SkillsService } from '../../../Services/Skills/skills.service';
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { NetworksService } from '../../../Services/Networks/networks.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

/* Models */
import { categories } from '../../../Interfaces/Constants/categories-constant';
import { projectStatus, needStatus } from '../../../Interfaces/Constants/status-constant';

/* Interfaces */
import { SearchModel } from '../../../Interfaces/search-models';
import { searchProject3 } from '../../../Interfaces/Constants/search-project.constant';

/* Library */
import * as _ from 'underscore';

@Component({
	selector: 'app-discover-search',
	templateUrl: './discover-search.component.html',
	styleUrls: [
		'./discover-search.component.scss',
		'../../../../public/styles/presentation.scss',
		'../../../../public/styles/project-card.scss',
		'../../../../public/styles/tooltip.scss'
	],
	animations: [
		trigger('fadeIn', [
      	state('shown', style({opacity: 1})),
			state('hidden', style({ opacity: 0 })),
			transition('shown => hidden', animate('0s')),
			transition('hidden => shown', animate('.5s'))
		])
	]
})
export class DiscoverSearchComponent implements OnInit {

	@Input() mobile 	: boolean
	categories			: Object[]
	statutes			: Object[]
	needStatutes 		: Object[]
	projects 			: Object[] = []
	currentStatus 		: string
	currentCategory 	: string
	currentNeed 		: string
	currentLocation 	: string
	placeholder 		: string
	skillName 			: string
	skills 				: Array<any>
	popularSkills 		: Array<any>
	selectedSkills 		: Array<any>
	searchSkills 		: Array<any>
	networks 			: Array<any>
	location 			: any
	searchBody 			: SearchModel
	limit 				: number
	currentRankType 	: string
	rankType 			: string
	selectedNetwork 	: string
	launched 			: boolean
	searching 			: boolean = false
	visibility = 'shown';
	priorities 			: Array<string> = ['status', 'category', 'opening', 'skills', 'location', 'network']

  constructor(private SkillsService: SkillsService,
              private ProjectsService: ProjectsService,
              private NetworksService: NetworksService,
              private TokenService: TokenService,
              private dialogService: DialogService,
              private PicturesService: PicturesService,
              private ActivatedRoute: ActivatedRoute,
              private ChangeDetectorRef: ChangeDetectorRef,
              @Inject(DOCUMENT) private document: any) {
  }

	ngOnInit() {
		this.initModels();
		this.initServices();
		this.getNetworks();
		this.initParams();
	}

	onScroll() {
		this.limit += 18
		this.getSearchBody('paginate', { value1: 18, value2: this.limit });
	}

	initModels() {
		this.skills 			= []
		this.selectedSkills		= []
		this.limit				= 18
		this.projects 			= []
		this.placeholder 		= 'Anything'
		this.currentStatus 		= 'All projects'
		this.currentCategory 	= 'Any category'
    	this.currentLocation 	= 'any location'
		this.currentNeed 		= needStatus[5].name
		this.categories 		= categories
		this.statutes 			= projectStatus
		this.needStatutes 		= needStatus
		this.searchBody 		= searchProject3
		this.currentRankType	= 'magic'
		this.rankType 			= 'popularity'
	}

	initServices() {
		this.searchBody.paginate.limit = 18
		this.getSkills();
		this.getNetworks();
	}

	initParams() {
		this.ActivatedRoute.queryParams.subscribe((params: Params) => {
			this.currentCategory 	= params.category ? params.category : 'Any category'
			this.currentStatus 		= params.status ? params.status : 'All projects'

			this.searchBody.query.members[0].value 	= this.currentStatus
			this.searchBody.query.members[1].value 	= this.currentCategory
			this.searchBody.paginate.limit 			= 18
			this.searchBody.paginate.offset 		= 0

			this.searchProjects(1);
			window.scrollTo(0, 0);
		});
	}

	searchProjects(index) {
		if (index)
			this.searching = true;
    // if (this.projects.length && this.projects.length === this.limit) return
		// else {
			this.ProjectsService.searchProject(this.searchBody).subscribe( res => {
				if (index)
					this.projects = []
				this.projects 	= this.projects.concat(res.projects);
				this.searching 	= false;
			});
		// }
	}

	getSkills() {
		this.SkillsService.getSkills().subscribe( res => {
			this.skills = res.skills
			this.popularSkills = this.skills.slice(0, 5);
		});
	}

	getNetworks() {
		this.NetworksService.getNetworks('university').subscribe( res => {
			this.networks = res.university
		});
	}

	getSearchBody(group, member) {
		let i = 0;
		if (group === 'members') {
			this.limit = 18
			this.searchBody.paginate.limit 	= 18
			this.searchBody.paginate.offset = 0
			this.searchBody.query.members[member.index].value = member.value
			this.searchBody.query.priority = this.priorities[member.index];
			i = 1;
		}
		if (group === 'sort') {
			const value = member.value === 'popularity' ? 'followers' : member.value
			this.searchBody.query.sort.field = value
			i = 1;
		}
		if (group === 'paginate') {
			this.searchBody.paginate.limit 	= member.value1,
			this.searchBody.paginate.offset = member.value2
		}
		this.searchProjects(i);
	}

	selectStatus(name: string, searchName: string) {
		this.currentStatus = name
		if (this.currentStatus === 'All projects')
			this.searchProjects(1);
		else
			this.getSearchBody('members', {index: 0, value: searchName});
	}

	selectCategory(name: string) {
		this.currentCategory = name
		if (this.currentCategory === 'Any category')
			this.searchProjects(1);
		else
			this.getSearchBody('members', {index: 1, value: name});
	}

	selectNeedStatus(name: string) {
		this.currentNeed = name
		if (this.currentNeed === 'for anything')
			this.searchProjects(1);
		else
			this.getSearchBody('members', {index: 2, value: name});
	}

	// selectSkill(name: string) {
	// 	if (this.selectedSkills.indexOf(name) < 0 && this.selectedSkills.length < 5) {
	// 		this.selectedSkills.push(name);
	// 		this.skillName = null;
	// 		this.getSearchBody('members', {index: 3, value: this.selectedSkills});
	// 	}
	// }

	removeSkill(name: string) {
		this.selectedSkills = _.without(this.selectedSkills, name);

		if (!this.searchSkills.length)
			this.searchSkills[0] = ''
		this.getSearchBody('members', {index: 3, value: this.searchSkills});
	}

	changePlaceholder() {
		if (this.placeholder !== 'add skill') {
			this.placeholder = 'add skill'
		} else {
			this.placeholder = 'Anything'
		}
	}

	getAddress(place: Object) {
		let address = place['address_components'][0].long_name + ', '
		if (!place['address_components'][3])
			address = address + "' ', " + place['address_components'][2].long_name
		else
			address = address + place['address_components'][2].short_name + ', ' + place['address_components'][3].long_name

    	this.getSearchBody('members', {index: 4, value: address});
	}

	changeRankType() {
		const old_type = this.currentRankType
		this.currentRankType = this.rankType
		this.rankType = old_type
    	this.getSearchBody('sort', {value: this.currentRankType});
	}

	/* TRANSFORM FUNCTION */
	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	transformNeedStatus(status) {
		if (status === 'for help')
			return 'any help'
		else if (status === 'for feedback')
			return 'feedback'
		else if (status === 'to hire someone')
			return 'hiring'
		else if (status === 'to hire an intern')
			return 'hiring interns'
		else if (status === 'for a cofounder')
			return 'cofounder'
	}

	/* NETWORK */
	selectNetwork(name: string, launched: boolean) {
		this.selectedNetwork = name
		this.launched = launched
		if (launched)
			this.getSearchBody('members', {index: 5, value: name});
	}

	removeNetwork() {
		this.selectedNetwork = ''
	}

	followProject(id, index, follow) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();

		this.ProjectsService.followProject(id, {}).subscribe( res => {
			if (res.success) {
				if (follow) {
					this.projects[index]['follow'] 		= 0
					this.projects[index]['followers']	-= 1
				} else {
					this.projects[index]['follow'] 		= 1
					this.projects[index]['followers']	+= 1
				}

			}
		});
	}

	/* SHOW MODAL */
	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
	}

	showSkillModal() {
		let disposable 	= this.dialogService.addDialog(SkillModalComponent, {
			selectedSkills : this.selectedSkills
		}).subscribe((skills: Array<string>) => {
			if (!skills[0])
				this.document.getElementById('wds-5').style.display = 'inline-block'

			this.selectedSkills = skills
			this.searchSkills 	= this.selectedSkills[0] ? this.selectedSkills : ['']

			this.getSearchBody('members', {index: 3, value: this.searchSkills});
		});
	}

	/* SHOW MOBILE MODAL */
	showStatusMobileModal() {
		let disposable = this.dialogService.addDialog(StatusMobileModalComponent, {
			statutes: this.statutes
		}).subscribe(status => {
			if (status)
				this.selectStatus(status['status'], status['searchName']);
		});
	}

	showCategoryMobileModal() {
		let disposable = this.dialogService.addDialog(CategoryMobileModalComponent, {
			categories: this.categories
		}).subscribe(category => {
			if (category)
				this.selectCategory(category);
		});
	}

	showNeedMobileModal() {
		let disposable = this.dialogService.addDialog(NeedMobileModalComponent, {
			needs: this.needStatutes
		}).subscribe(need => {
			if (need) {
				if (need === 'Any help' || need === 'Teammate' || need === 'Mentor')
					this.showSkillMobileModal();
				this.selectNeedStatus(need);
			}
		});
	}

	showSkillMobileModal() {
		let disposable = this.dialogService.addDialog(SkillMobileModalComponent, {
			skills: this.skills
		}).subscribe(skills => {
			if (skills)
				this.getSearchBody('members', {index: 3, value: skills});
		});
	}

	showLocationMobileModal() {
		let disposable = this.dialogService.addDialog(LocationMobileModalComponent, {
			noVariable: 'hello'
		}).subscribe(location => {
			if (location) {
				this.currentLocation = location;
				this.getSearchBody('members', {index: 4, value: location});
				this.ChangeDetectorRef.detectChanges();
			}
		});
	}
}



