import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';

/* Custom Modules */
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';

/* Components */
import { DiscoverComponent } from './discover.component';
import { DiscoverSearchComponent } from './discover-search/discover-search.component';

// import { MetaService } from '../../Services/meta.service';

/* Directives */
import { DiscoverDirective } from '../../Directives/Discover/discover.directive';
import { DiscoverDirectiveSub1 } from '../../Directives/Discover/discover-sub1.directive';
import { DiscoverDirectiveWDS4 } from '../../Directives/Discover/discover-wds4.directive';
import { DirectivesModule } from '../../Directives/directives.module';

/* Librairies */

/* Route */
import { DISCOVER_ROUTES } from '../../Routes/discover.routes';

@NgModule({
	imports: [
		DISCOVER_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		PipeModule,
		FooterModule,
		DirectivesModule,
		InfiniteScrollModule
	],
	declarations: [
		DiscoverComponent,
		DiscoverSearchComponent,
		DiscoverDirective,
		DiscoverDirectiveSub1,
		DiscoverDirectiveWDS4
	],
	providers: [],
	exports: [
	]
})
export class DiscoverModule { }



