import { Component, OnInit, HostListener, Inject } from '@angular/core';
import { DOCUMENT, Title } from "@angular/platform-browser";
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { InvitationSocialComponent } from '../modals/invitation-social/invitation-social.component';

/* Services */
import { SkillsService } from '../../Services/Skills/skills.service';
import { ProjectsService } from '../../Services/Projects/projects.service';
import { NetworksService } from '../../Services/Networks/networks.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { SharedService } from '../../Services/shared.service';
import { MetaService } from '../../Services/meta.service';
import { TrackingService } from '../../Services/Trackings/tracking.service';

/* Animations */
import { popDiscover } from '../../Animations/animations';

export const textScrollDiscover = [
	'Let us find the projects looking to connect with you. Join the best community on the Internet.',
	'It takes less than 20 seconds',
	'Why are you still waiting? 🙈'
]

@Component({
	selector: 'app-discover',
	templateUrl: './discover.component.html',
	styleUrls: [
		'./discover.component.scss',
		'../../../public/styles/presentation.scss'],
	providers: [SkillsService, ProjectsService, NetworksService, PicturesService],
	animations: [popDiscover]
})
export class DiscoverComponent implements OnInit {

	mobile 		: boolean = false
	showPop 	: boolean = false
	log 		: boolean = false
	alreadyPop 	: boolean = false
	notPop 		: boolean = false
	popState 	: string
	text 		: string

	constructor(private dialogService: DialogService,
				private TokenService: TokenService,
				private SharedService: SharedService,
				private MetaService: MetaService,
				private Title: Title,
				private TrackingService: TrackingService,
		@Inject(DOCUMENT) private document: any) {
	}

	ngOnInit() {
		this.initWindow();
		this.initToken();
		// this.text = textScrollDiscover[0]
		this.setMetaData();
		this.trackViewPage();
	}

	trackViewPage() {
		const token = this.TokenService.getToken();

		this.TrackingService.viewActivities({}, token, 6).subscribe(r => {});
	}

	setMetaData() {
		this.Title.setTitle('Witty | Discover');
		this.MetaService.setMeta('description', "Witty is the professional network for the new entrepreneurial age. We connect millions of people to what's being built around them, including you.");

		this.MetaService.setMeta('og:title', "Explore what's being built around you, now - Witty");
		this.MetaService.setMeta('og:description', "Witty is the professional network for the new entrepreneurial age. We connect millions of people to what's being built around them, including you.");
		this.MetaService.setMeta('og:url', 'https://www.wittycircle.com/discover');
		this.MetaService.setMeta('og:image', 'http://res.cloudinary.com/dqpkpmrgk/image/upload/v1508448762/Share_Link_Cards_Facebook/Thanks_But_No_Thanks_White_Bckgrd-01.png');
	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	initToken() {
		if (this.TokenService.getToken() && this.TokenService.getToken().user)
			this.log 	= true
	}

	signIn() {
		this.notShowPop();
		this.SharedService.setSignUpStatus(true);
	}

	notShowPop() {
		this.notPop 	= true
		this.popState 	= ''
		this.showPop 	= false
		this.alreadyPop = false

		setTimeout(() => {
			this.notPop = false
		}, 1000 * 10)
	}

	showInvitationModal() {
		window.scrollTo(0, 0);
		this.notShowPop();

		let disposable 	= this.dialogService.addDialog(InvitationSocialComponent, {
        	check : true
    	}).subscribe( success => {
    		this.notPop = false
    	});
	}

	/* ON SCROLL EVENT */
	@HostListener("window:scroll", [])
	onWindowScroll() {
		// if (!this.log) {
			let number = this.document.documentElement.scrollTop;

			if (!this.notPop) {
				if (number > 50 && !this.alreadyPop) {
					if (this.popState !== 'up1') {
						// this.text 		= textScrollDiscover[0]
						this.popState 	= 'up1'
						this.showPop 	= true
						this.alreadyPop = true
					}
				} else if (number <= 50 && this.alreadyPop) {
					this.popState 	= ''
					this.showPop 	= false
					this.alreadyPop = false
				}
			}
			// if (number > 50) {
			// 	if (this.popState !== 'up1') {
			// 		// this.text 		= textScrollDiscover[0]
			// 		this.popState 	= 'up1'
			// 		this.showPop 	= true
			// 	}
			// } else if (number > 300 && number < 600) {
			// 	if (this.popState !== 'up2') {
			// 		this.popState 	= 'up2'
			// 		// this.text 		= textScrollDiscover[1]
			// 	}
			// } else if (number > 600 && number < 900) {
			// 	if (this.popState !== 'up3') {
			// 		this.popState 	= 'up3'
			// 		// this.text 		= textScrollDiscover[2]
			// 	}
			// } else if (number <= 50) {
			// 	this.popState 	= ''
			// 	this.showPop 	= false
			// }
		// }
	}
}



